<template>
  <div :style="{height:'100%',width:'100%'}">
    <div :id="networkId" :style="{height:'100%',width:'100%'}" ></div>
    <div id="contextMenuContainer" @contextmenu.prevent>
      <ContextMenu :key="contextMenuItem" v-bind:menu-item="contextMenuItem" @menu-select-event="handleContextMenuClickEvent"></ContextMenu>
    </div>
  </div>

</template>

<script>
const vis = require("vis-network/dist/vis-network.min.js");
require("vis-network/dist/dist/vis-network.min.css");//dist/vis-network.min.css
import ContextMenu from "@/components/ContextMenu";

export default {
  name: "CompareNetworkVis",
  components: {ContextMenu},
  props:{
    enableEdit:{
      type: Boolean,
      default: false
    },
    position: {
      type: Object,
      default(){
        return {
          isLeft: false,
          isRight: false
        }
      }
    },
    testEditData:Object
  },
  data(){
    return {
      visData: null,
      visOption:  {
        autoResize: true,
        groups:{
          add: {
            color: {
              border: "#fff",
              background: "#00FF00",
              highlight: {
                border: "#ffffff",
                background: "#343C96"
              },
              hover: {
                border: "#ffffff",
                background: "#6778B7"
              }
            }
          },
          modify: {
            color: {
              border: "#fff",
              background: "#FFD700",
              highlight: {
                border: "#ffffff",
                background: "#343C96"
              },
              hover: {
                border: "#ffffff",
                background: "#6778B7"
              }
            }
          },
          delete: {
            color: {
              border: "#fff",
              background: "#FF6A6A",
              highlight: {
                border: "#ffffff",
                background: "#343C96"
              },
              hover: {
                border: "#ffffff",
                background: "#6778B7"
              }
            }
          },
          concept: {
            color: {
              border: "#fff",
              background: "#D3D3D3",
              highlight: {
                border: "#ffffff",
                background: "#343C96"
              },
              hover: {
                border: "#ffffff",
                background: "#6778B7"
              }
            }
          },
          property: {
            shape: "box",
            color: {
              border: "#000000",
              background: "#75B9A9",
              highlight: {
                border: "#ffffff",
                background: "#338756"
              },
              hover: {
                border: "#ffffff",
                background: "#60AE77"
              }
            }
          },
          instance: {
            color: {
              border: "#000000",
              background: "#A66877",
              highlight: {
                border: "#ffffff",
                background: "#6B2F52"
              },
              hover: {
                border: "#ffffff",
                background: "#965884"
              }
            }
          },
          relation: {
            shape: "diamond",
            color: {
              border: "#000000",
              background: "#9FB06E",
              highlight: {
                border: "#ffffff",
                background: "#797331"
              },
              hover: {
                border: "#ffffff",
                background: "#A4945A"
              }
            }
          },
          tag: {
            shape: "triangle",
            color: {
              border: "#000000",
              background: "#7EC2D4",
              highlight: {
                border: "#ffffff",
                background: "#3891A7"
              },
              hover: {
                border: "#ffffff",
                background: "#62A2CB"
              }
            }
          }
        },
        nodes: {
          shape: 'dot',
          size: 24,
          font: {
            face: "PingFang SC",
            size: 13
          },
          shadow: {
            enabled: !1,
            color: "rgba(0,0,0,0.5)",
            size: 16,
            x: 6,
            y: 6
          },
          borderWidth: 2,
          borderWidthSelected: 4,
          color: {
            border: '#2B7CE9',
            background: '#97C2FC',
            highlight: {
              border: '#2B7CE9',
              background: '#D2E5FF'
            },
          }
        },
        edges: {
          width: 2,
          smooth:{  //设置两个节点之前的连线的状态
            enabled: true  //默认是true，设置为false之后，两个节点之前的连线始终为直线，不会出现贝塞尔曲线
          },
          arrows: {
            to : {
              enabled: true,
              type: "arrow"
            }
          },
          color: {
            color: "#bcbcbc"
          },
          font: {
            size: 8,
            face: "tahoma",
            strokeWidth: 1
          },
        },
        physics: { //计算节点之前斥力，进行自动排列的属性
          enabled: true, //默认是true，设置为false后，节点将不会自动改变，拖动谁谁动。不影响其他的节点
          barnesHut: {
            gravitationalConstant: -4000,
            centralGravity: 0.3,
            springLength: 120,
            springConstant: 0.04,
            damping: 0.09,
            avoidOverlap: 0
          }
        },
        interaction:{
          dragNodes: true, //是否能拖动节点
          dragView: true, //是否能拖动画布
          hover: true, //鼠标移过后加粗该节点和连接线
          multiselect: true, //按 ctrl 多选
          selectable: true, //是否可以点击选择
          selectConnectedEdges: true, //选择节点后是否显示连接线
          hoverConnectedEdges: true, //鼠标滑动节点后是否显示连接线
          zoomView: true //是否能缩放画布
        },
        manipulation: {  //该属性表示可以编辑，出现编辑操作按钮
          enabled: false,
          editNode: function (nodeData, callback){
            console.log(nodeData);
            callback(nodeData);
          }
        }
      },
      contextMenuData: {
        value: null
      },
      contextMenuItem: null,
      editNodePanelVisible: false,
      tempEditNode: null,
    }
  },
  computed: {
    newVisData(){
      return this.position.isLeft ? this.$store.state.compareGraphVisData.newVisDataLeft : this.$store.state.compareGraphVisData.newVisDataRight;
    },
    selectedNode(){
      return this.$store.state.compareGraphVisData.selectedNode;
    },
    expandedNode(){
      return this.$store.state.compareGraphVisData.expandedNode;
    },
    networkId(){
      return this.position.isLeft ? "network-left" : "network-right";
    }
  },
  methods:{
    selectNode(nodeId){
      this.visNetwork.selectNodes([nodeId]);
    },
    initVisNetwork: function (){
      this.visData = {
        nodeIds:[],
        nodes: new vis.DataSet([]),
        edgeIds:[],
        edges: new vis.DataSet([]),
      };
      if(this.position.isLeft){
        this.visNetwork = new vis.Network(document.getElementById(  "network-left"),this.visData, this.visOption);
      }else if(this.position.isRight){
        this.visNetwork = new vis.Network(document.getElementById(  "network-right"),this.visData, this.visOption);
      }
      this.initNetworkEvent();
    },
    initNetworkEvent: function (){
      let this_ = this;
      this.visNetwork.on("oncontext", function (param){
        console.log(param)
        const menu = document.getElementById("contextMenuContainer");
        menu.style.display = "block";
        menu.style.left = param.event.clientX + 'px';
        menu.style.top = param.event.clientY + 'px';
        param.event.stopPropagation();
        param.event.preventDefault();
        const nodeId = this_.visNetwork.getNodeAt(param.pointer.DOM);
        const edgeId = this_.visNetwork.getEdgeAt(param.pointer.DOM);
        if(typeof nodeId !== "undefined"){
          const node = this_.visData.nodes.get(nodeId);
          this_.contextMenuData = {
            type: 'node',
            value: node
          };
          this_.contextMenuItem = ['edit', 'delete', 'addEdge']
        }
        else if(typeof edgeId !== "undefined"){
          const edge = this_.visData.edges.get(edgeId);
          this_.contextMenuData = {
            type: 'edge',
            value: edge
          };
          this_.contextMenuItem = ['edit', 'delete']
        }else{
          this_.contextMenuItem = ['addNode']
        }
      });
      this.visNetwork.on("doubleClick",function (params) {
        let nodeId = this_.visNetwork.getNodeAt(params.pointer.DOM);
        //let edgeId = this_.visNetwork.getEdgeAt(params.pointer.DOM);
        //this_.switchNodeExpandState(nodeId);
        this_.$store.state.compareGraphVisData.expandedNode = JSON.parse(JSON.stringify({
          nodeId: nodeId
        }));
      });
      this.visNetwork.on("click",function (params) {
        let nodeId = this_.visNetwork.getNodeAt(params.pointer.DOM);
        //let edgeId = this_.visNetwork.getEdgeAt(params.pointer.DOM);
        this_.$store.state.compareGraphVisData.selectedNode = nodeId;
        console.log("单机的节点",nodeId,this_.visData.nodes.get(nodeId));
      });
    },
    switchNodeExpandState(nodeId, isExpanded){
      const flag = typeof isExpanded !== "undefined" || isExpanded === null;
      console.log(flag, isExpanded)
      if(typeof nodeId !== "undefined"){
        let node = this.visData.nodes.get(nodeId);
        if(!node.expanded){
          if(flag){
            if(isExpanded){
              this.addData(node.children);
              node.expanded = true;
            }
          }else {
            this.addData(node.children);
            node.expanded = true;
          }
        }else {
          if(flag){
            if(!isExpanded){
              this.removeData(node.children);
              node.expanded = false;
            }
          }else {
            this.removeData(node.children);
            node.expanded = false;
          }
        }
      }
    },
    getGroup(id, type){
      if(type === 'node'){
        if(this.testEditData.addNode.indexOf(id) !== -1) return 'add';
        else if(this.testEditData.modifyNode.indexOf(id) !== -1) return 'modify';
        else if(this.testEditData.deleteNode.indexOf(id) !== -1) return 'delete';
      }else if(type === 'edge'){
        if(this.testEditData.addEdge.indexOf(id) !== -1) return 'add';
        else if(this.testEditData.deleteEdge.indexOf(id) !== -1) return 'delete';
      }
      return 'concept';
    },
    addData (newData){
      console.log(newData)
      const this_ = this;
      newData.nodes.forEach((N) =>{
        N.label = N.name;
        if(this_.position.isLeft && N.id === 76){
          N.label = "工商号"
        }
        N.group = this.getGroup(N.id, 'node')
        if(this_.position.isLeft){
          if(N.group === 'add'){
            return;
          }
        }else if(this_.position.isRight){
          if(N.group === 'delete'){
            return;
          }
        }
        console.log(N.id, N.group)
        if(this_.visData.nodeIds.indexOf(N.id) === -1){
          this_.visData.nodes.add(N);
          this_.visData.nodeIds.push(N.id);
        }
      });
      if (typeof newData.relationships[0].relationArray === "undefined"){
        newData.relationships.forEach((SN) =>{
            SN.label = SN.ifSuperClass ? "是一种" :  SN.name;
            SN.group = this.getGroup(SN.id, 'edge')
            if(typeof SN.from ==="undefined" || SN.from === null){
              SN.from = SN.source;
              SN.to = SN.target;
            }
            console.log(SN)
            if(this_.visData.edgeIds.indexOf(SN.id) === -1){
              this_.visData.edges.add(SN);
              this_.visData.edgeIds.push(SN.id);
            }
        });
      }else
        newData.relationships.forEach((N) =>{
          N.relationArray.forEach((SN)=>{
            SN.label = SN.ifSuperClass ? "是一种" :  SN.name;
            SN.group = this.getGroup(SN.id, 'edge')
            if(typeof SN.from ==="undefined" || SN.from === null){
              SN.from = SN.source;
              SN.to = SN.target;
            }
            console.log(SN)
            if(this_.visData.edgeIds.indexOf(SN.id) === -1){
              this_.visData.edges.add(SN);
              this_.visData.edgeIds.push(SN.id);
            }
          })
        });
    },
    removeData(data){
      const this_ = this;
      if(data.nodes){
        data.nodes.forEach((N) =>{
          if(this_.visData.nodeIds.indexOf(N.id) !== -1){
            this_.visData.nodes.remove(N);
            this_.visData.nodeIds.splice(this_.visData.nodeIds.indexOf(N.id), 1);
          }
        });
      }
      if(data.edges){
        data.edges.forEach((N) =>{
          if(this_.visData.edgeIds.indexOf(N.id) !== -1){
            this_.visData.edges.remove(N);
            this_.visData.edgeIds.splice(this_.visData.edgeIds.indexOf(N.id), 1);
          }
        });
      }
    },
    handleContextMenuClickEvent: function (action, data){
      console.log("右键菜单选择", action, data);
      if(action === "delete"){
        const removeData = this.contextMenuData.type === 'node' ? {nodes:[this.contextMenuData.value]} :{ edges:[this.contextMenuData.value]};
        this.removeData(removeData);
        console.log(removeData)
      }else if(action == "edit"){
        console.log("点击编辑菜单")
        this.editNodePanelVisible = true;
      }
    }
  },
  watch: {
    newVisData:{
      immediate: true,
      handler: function() {
        if(this.newVisData && this.newVisData.nodes){
          this.addData(this.deepCopy(this.newVisData));
        }
      }
    },
    selectedNode(){
      this.selectNode(this.selectedNode);
    },
    expandedNode(){
      this.switchNodeExpandState(this.expandedNode.nodeId);
    }
  },
  mounted() {
    this.initVisNetwork();
  },
}
</script>

<style scoped>
#contextMenuContainer{
  position: absolute;
  left: 500px;
  top: 500px;
  display: none;
}


#network{
  outline: none;
}
</style>
<style>
.vis-network{
  outline: none;
}
</style>