<template>
  <li
      class="el-dropdown-menu__item"
      :class="{
      'is-disabled': disabled,
      'el-dropdown-menu__item--divided': divided
    }"
      @click="handleClick"
      :aria-disabled="disabled"
      :tabindex="disabled ? null : -1"
  >
    <i :class="icon" v-if="icon"></i>
    <slot></slot>
  </li>
</template>
<script>
import Emitter from 'element-ui/src/mixins/emitter';

export default {
  name: 'MyMenuItem',

  mixins: [Emitter],

  props: {
    command: {},
    disabled: Boolean,
    divided: {
      type: Boolean,
    },
    icon: String,
    action: String
  },

  methods: {
    handleClick() {
      this.$emit("my-menu-item-click", this.action);
    }
  }
};
</script>
