<template>
  <div style="padding: 5px">
    <el-input
        placeholder="输入关键字进行过滤"
        v-model="filterText"
        size="mini">
    </el-input>

    <el-tree
        class="filter-tree"
        :data="showData"
        :props="defaultProps"
        lazy
        :highlight-current=true
        :expand-on-click-node="false"
        @node-drag-start="handleDragStart"
        @node-drag-enter="handleDragEnter"
        @node-drag-leave="handleDragLeave"
        @node-drag-over="handleDragOver"
        @node-drag-end="handleDragEnd"
        @node-drop="handleDrop"
        @node-click="handleClick"
        draggable
        :allow-drop="allowDrop"
        :allow-drag="allowDrag"
        :filter-node-method="filterNode"
        :load="loadNode"
        ref="tree">
      <span class="custom-tree-node" slot-scope="{ node, data }">
        <span>{{ node.label }}</span>
        <span>
          <el-button
              type="text"
              size="mini"
              @click="() => appendNode(data)"
              icon="el-icon-circle-plus-outline">
          </el-button>
          <el-button
              type="text"
              size="mini"
              @click="() => editNode(node, data)"
              icon="el-icon-edit-outline">
          </el-button>
          <el-button
              type="text"
              size="mini"
              @click="() => removeNode(node, data)"
              icon="el-icon-circle-close">
          </el-button>
        </span>
      </span>
    </el-tree>
  </div>
</template>

<script>
export default {
  name: "TreeEdit",
  props: {
  },
  data() {
    return {
      filterText: '',
      showData: [],
      defaultProps: {
        label: 'label'
      }
    };
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    newVisData(){
      this.addShowData(this.deepCopy(this.newVisData));
    },
    selectedNode(){

    }
  },
  methods: {
    appendNode(data){
      console.log(data)
    },
    editNode(node, data){
      console.log(node, data)
    },
    removeNode(node, data){
      console.log(node, data)
    },
    handleClick(data, node, element){
      console.log(data, node, element);
      const nodePath = [];
      while(node !== null && node.level >= 1){
        console.log(node, node.data.id)
        nodePath.push({
          id: node.data.id,
          label: node.data.label
        });
        node = node.parent;
      }
      this.$store.state.graphVisData.selectedNode = nodePath;
    },
    loadNode(node, resolve){
      console.log(node);
      const nodeData = node.data;
      if(nodeData && nodeData.childrenSrc && nodeData.childrenSrc.nodes && nodeData.childrenSrc.nodes.length > 0){
        const data = nodeData.childrenSrc.nodes;
        data.forEach((N)=>{
          N.label = N.name;
          N.childrenSrc = N.children;
          N.children = [];
        })
        return resolve(data);
      }
      return resolve([])
    },
    addShowData(newData){ // eslint-disable-line no-unused-vars
      const copyNewData = JSON.parse(JSON.stringify(newData))
      copyNewData.nodes.forEach((N)=>{
        if(this.indexOf(this.showData, N, (N)=>N.id) === -1){
          N.label = N.name;
          N.childrenSrc = N.children;
          N.children = [];
          this.showData.push(N);
        }
      })
    },
    handleDragStart(node, ev) {
      console.log('drag start', node, ev);
    },
    handleDragEnter(draggingNode, dropNode, ev) {
      console.log('tree drag enter: ', dropNode.label, ev);
    },
    handleDragLeave(draggingNode, dropNode, ev) {
      console.log('tree drag leave: ', dropNode.label, ev);
    },
    handleDragOver(draggingNode, dropNode, ev) {
      console.log('tree drag over: ', dropNode.label, ev);
    },
    handleDragEnd(draggingNode, dropNode, dropType, ev) {
      console.log('tree drag end: ', dropNode && dropNode.label, dropType, ev);
    },
    handleDrop(draggingNode, dropNode, dropType, ev) {
      console.log('tree drop: ', dropNode.label, dropType, ev);
    },
    allowDrop(draggingNode, dropNode, type) {
      if (dropNode.data.label === '二级 3-1') {
        return type !== 'inner';
      } else {
        return true;
      }
    },
    allowDrag(draggingNode) {
      return draggingNode.data.label.indexOf('三级 3-2-2') === -1;
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    }
  },
  computed:{
    newVisData() {
      return this.$store.state.graphVisData.newVisData;
    },
    selectedNode(){
      return this.$store.state.graphVisData.selectedNode;
    }
  }
};

</script>

