<template>
  <el-dialog :title="dialogData.title" :visible.sync="dialogData.visible">
    <el-cascader
        v-model="selectedCompareVersions.versionLeft"
        :options="dialogData.options"
        :props="{ expandTrigger: 'hover' }"
        ></el-cascader>
    <el-cascader
        v-model="selectedCompareVersions.versionRight"
        :options="dialogData.options"
        :props="{ expandTrigger: 'hover' }"
        ></el-cascader>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogData.visible = false">取 消</el-button>
      <el-button type="primary" @click="dialogSubmit">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "CompareVersionSelector",
  methods: {
    dialogSubmit: function (){
      // if(!this.selectedCompareVersions.versionLeft || !this.selectedCompareVersions.versionRight){
      //
      // }
      this.dialogData.visible = false;
      this.$emit("on-compare-version-select-event", this.selectedCompareVersions)
    }
  },
  props:{
    dialogData:{
      type: Object,
      default(){
        return {
          title: "版本选择",
          visible: true,
          options:[{
            value:"public",
            label:"公有",
            selectable: false,
            children:[{
              value:"1.0",
              label:"1.0",
              selectable: true,
            }]
          }]
        }
      }
    }
  },
  data(){
    return {
      selectedCompareVersions: {
        versionLeft: null,
        versionRight: null
      },
    }
  }
}
</script>

