import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Main from '@/page/Main'
import Compare from "@/page/Compare";
import axios from "axios";
import qs from "qs";
import Vuex from 'vuex'


Vue.use(VueRouter)
Vue.use(ElementUI)
Vue.use(axios)
Vue.use(qs)
Vue.use(Vuex)
Vue.prototype.$qs = qs
Vue.prototype.$axios = axios
Vue.prototype.deepCopy = function (obj){
  return JSON.parse(JSON.stringify(obj));
}
Vue.prototype.indexOf = function(array, element, func) {
  if(typeof func==="function"){
    for(let i=0;i<array.length;i++){
      if(element===func(array[i])){
        return i;
      }
    }
    return -1;
  }
  else{
    for(let i=0;i<array.length;i++){
      if(element===array[i]){
        return i;
      }
    }
    return -1;
  }
}

axios.defaults.baseURL = "/ontoApi"

Vue.config.productionTip = false

// 定义路由
// 每个路由应该映射一个组件。 其中"component" 可以是
// 通过 Vue.extend() 创建的组件构造器，
// 或者，只是一个组件配置对象。
// 我们晚点再讨论嵌套路由。
const routes = [
  {
    path: '/',
    name: '主页',
    redirect: '/main'
  },
  {
    path: '/main',
    component: Main
  },
  {
    path: '/compare',
    component: Compare
  }
]
const store = new Vuex.Store({
  state:{
    coreConcepts:[],
    tags:[],
    graphVisData: {
      selectedNode: null,
      selectedEdge: null,
      newVisData: null,
    },
    compareGraphVisData: {
      selectedNode: null,
      expandedNode: null,
      newVisDataLeft: null,
      newVisDataRight: null,
    },
    trustworthinessViewSetting:{
      enable: false,
      trustworthinessLevel:[0,1],
      trustworthinessValue:{
        trustworthinessValue: {
          enable: true,
          value:[0,1]
        },
        reliability: {
          enable: true,
          value:[0,1]
        },
        correctness:{
          enable: true,
          value:[0,1]
        },
        timeSensitive:{
          enable: true,
          value:[0,1]
        },
        security:{
          enable: true,
          value:[0,1]
        },
        accessibility:{
          enable: true,
          value:[0,1]
        }
      },
      active: 'level'
    },
    isTestMode: false
  }
})

// 3. 创建 router 实例，然后传 `routes` 配置
// 你还可以传别的配置参数, 不过先这么简单着吧。
const router = new VueRouter({
  routes // (缩写) 相当于 routes: routes
})

// 4. 创建和挂载根实例。
// 记得要通过 router 配置参数注入路由，
// 从而让整个应用都有路由功能
new Vue({
  render: h => h(App),
  router,
  store,
}).$mount('#app');
