<template>
  <div :style="{height:'100%',width:'100%',position: 'relative'}">
    <div id="network" :style="{height:'100%',width:'100%'}" ></div>
    <NetworkSettingToolbar @expand-all-node-event="handleExpandAllNode" @vis-option-update-event="updateVisOption" v-bind:vis-option="visOption" :style="{position:'absolute',top:'0', right:'5px'}"></NetworkSettingToolbar>
    <ElementDetailPanel :style="{position:'absolute',top:'60px',right:'35px'}"></ElementDetailPanel>

    <div id="contextMenuContainer" @contextmenu.prevent>
      <!--  :key="contextMenuItem"     -->
      <ContextMenu v-bind:menu-item="contextMenuItem" @menu-select-event="handleContextMenuClickEvent"></ContextMenu>
    </div>
    <NodeEditPanel @edit-node-submit-event="handleNodeEditSubmit" :edit-node-panel-visible="editNodePanelVisible"  :node-data="contextMenuData.value"></NodeEditPanel>

    <el-dialog :title="nodeEditType" :visible.sync="editEdgePanelVisible" :width="'30%'">
      <EdgeEditPanel @edit-node-event="handleEditEdgeEvent"  :node-data="contextMenuData.value"></EdgeEditPanel>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editEdgePanelVisible = false">取 消</el-button>
        <el-button type="primary" @click="editEdgePanelVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
import NetworkSettingToolbar from "@/components/NetworkSettingToolbar";
const vis = require("vis-network/dist/vis-network.min.js");
require("vis-network/dist/dist/vis-network.min.css");//dist/vis-network.min.css
import ContextMenu from "@/components/ContextMenu";
import NodeEditPanel from "@/components/NodeEditPanel";
import EdgeEditPanel from "@/components/EdgeEditPanel";
import ElementDetailPanel from "@/components/ElementDetailPanel"
export default {
  name: "NetworkVis",
  components: {EdgeEditPanel, NetworkSettingToolbar, ContextMenu,NodeEditPanel,ElementDetailPanel},
  props:{
    enableEdit:{
      type: Boolean,
      default: true
    }
  },
  data(){
    return {
      //visNetwork: null,
      visData: null,
      visOption:  {
        autoResize: true,
        groups:{
          concept: {
            color: {
              border: "#fff",
              background: "#8F7DC1",
              highlight: {
                border: "#ffffff",
                background: "#343C96"
              },
              hover: {
                border: "#ffffff",
                background: "#6778B7"
              }
            }
          },
          property: {
            shape: "box",
            color: {
              border: "#000000",
              background: "#75B9A9",
              highlight: {
                border: "#ffffff",
                background: "#338756"
              },
              hover: {
                border: "#ffffff",
                background: "#60AE77"
              }
            }
          },
          instance: {
            color: {
              border: "#000000",
              background: "#A66877",
              highlight: {
                border: "#ffffff",
                background: "#6B2F52"
              },
              hover: {
                border: "#ffffff",
                background: "#965884"
              }
            }
          },
          relation: {
            shape: "diamond",
            color: {
              border: "#000000",
              background: "#9FB06E",
              highlight: {
                border: "#ffffff",
                background: "#797331"
              },
              hover: {
                border: "#ffffff",
                background: "#A4945A"
              }
            }
          },
          tag: {
            shape: "triangle",
            color: {
              border: "#000000",
              background: "#7EC2D4",
              highlight: {
                border: "#ffffff",
                background: "#3891A7"
              },
              hover: {
                border: "#ffffff",
                background: "#62A2CB"
              }
            }
          }
        },
        nodes: {
          shape: 'dot',
          size: 24,
          font: {
            face: "PingFang SC",
            size: 13
          },
          shadow: {
            enabled: !1,
            color: "rgba(0,0,0,0.5)",
            size: 16,
            x: 6,
            y: 6
          },
          borderWidth: 2,
          borderWidthSelected: 4,
          color: {
            border: '#2B7CE9',
            background: '#97C2FC',
            highlight: {
              border: '#2B7CE9',
              background: '#D2E5FF'
            },
          }
        },
        edges: {
          width: 2,
          smooth:{  //设置两个节点之前的连线的状态
            enabled: true  //默认是true，设置为false之后，两个节点之前的连线始终为直线，不会出现贝塞尔曲线
          },
          arrows: {
            to : {
              enabled: true,
              type: "arrow"
            }
          },
          color: {
            color: "#bcbcbc"
          },
          font: {
            size: 8,
            face: "tahoma",
            strokeWidth: 1
          },
        },
        physics: { //计算节点之前斥力，进行自动排列的属性
          enabled: true, //默认是true，设置为false后，节点将不会自动改变，拖动谁谁动。不影响其他的节点
          barnesHut: {
            gravitationalConstant: -2000,
            centralGravity: 0.3,
            springLength: 120,
            springConstant: 0.04,
            damping: 0.09,
            avoidOverlap: 0
          }
        },
        interaction:{
          dragNodes: true, //是否能拖动节点
          dragView: true, //是否能拖动画布
          hover: true, //鼠标移过后加粗该节点和连接线
          multiselect: true, //按 ctrl 多选
          selectable: true, //是否可以点击选择
          selectConnectedEdges: true, //选择节点后是否显示连接线
          hoverConnectedEdges: true, //鼠标滑动节点后是否显示连接线
          zoomView: true //是否能缩放画布
        },
        manipulation: {  //该属性表示可以编辑，出现编辑操作按钮
          enabled: false,
          editNode: function (nodeData, callback){
            console.log(nodeData);
            callback(nodeData);
          }
        }
      },
      contextMenuData: {
        type: null,
        value: null
      },
      contextMenuItem: [],
      editNodePanelVisible: false,
      editEdgePanelVisible:false,
      tempEditNode: null,
      tempEditEdge: null,
      nodeEditType:""
    }
  },
  computed: {
    newVisData(){
      return this.$store.state.graphVisData.newVisData;
    },
    selectedNode(){
      return this.$store.state.graphVisData.selectedNode;
    }
  },
  methods:{
    handleExpandAllNode(){
      const this_ = this;
      console.log(this);
      console.log(this.visData.nodes)
      this.visData.nodes.forEach((N)=>{
        console.log(N);
        this_.switchNodeExpandState(N, true);
      })
    },
    handleNodeEditSubmit(nodeData){
      this.editNodePanelVisible = false;
      const N = JSON.parse(JSON.stringify(nodeData));
      N.label = N.name;
      N.group = "concept";
      if(this.visData.nodeIds.indexOf(N.id) === -1){
        this.visData.nodes.add(N);
        this.visData.nodeIds.push(N.id);
      }
    },
    updateVisOption(newVisOption){
      this.visOption = newVisOption;
      if(typeof  this.visNetwork !== "undefined"){
        this.visNetwork.setOptions(this.visOption);
        this.visNetwork.startSimulation();
      }
    },
    selectNodeByPath(nodePath){
      for (let i = nodePath.length - 1; i >= 1; i--) {
        this.switchNodeExpandState(nodePath[i].id, true);
        //const node = this.visData.nodes.get(nodePath[i].id);
      }
      this.visNetwork.selectNodes([nodePath[0].id]);
    },

    handleEditEdgeEvent(edgeData){
      this.tempEditEdge = edgeData;
    },
    initVisNetwork: function (){
      this.visData = {
        nodeIds:[],
        nodes: new vis.DataSet([]),
        edgeIds:[],
        edges: new vis.DataSet([]),
      };
      this.visNetwork = new vis.Network(document.getElementById("network"),this.visData, this.visOption);
      this.initNetworkEvent();
    },
    initNetworkEvent: function (){
      let this_ = this;
      this.visNetwork.on("oncontext", function (param){
        console.log(param)
        const menu = document.getElementById("contextMenuContainer");
        menu.style.display = "block";
        menu.style.left = param.event.offsetX + 'px';
        menu.style.top = param.event.offsetY +'px';
        param.event.stopPropagation();
        param.event.preventDefault();

        const nodeId = this_.visNetwork.getNodeAt(param.pointer.DOM);
        const edgeId = this_.visNetwork.getEdgeAt(param.pointer.DOM);
        if(typeof nodeId !== "undefined"){
          const node = this_.visData.nodes.get(nodeId);
          this_.contextMenuData = {
            type: 'node',
            value: node
          };
          console.log(this_.contextMenuData)
          this_.contextMenuItem = ['edit', 'delete', 'addEdge']
        }
        else if(typeof edgeId !== "undefined"){
          const edge = this_.visData.edges.get(edgeId);
          this_.contextMenuData = {
            type: 'edge',
            value: edge
          };
          this_.contextMenuItem = ['edit', 'delete']
        }else{
          this_.contextMenuItem = ['addNode']
        }
      });
      this.visNetwork.on("doubleClick",function (params) {
        let nodeId = this_.visNetwork.getNodeAt(params.pointer.DOM);
        //let edgeId = this_.visNetwork.getEdgeAt(params.pointer.DOM);
        if(typeof nodeId !== "undefined"){
          this_.switchNodeExpandState(nodeId);
        }
      });
      this.visNetwork.on("click",function (params) {
        let nodeId = this_.visNetwork.getNodeAt(params.pointer.DOM);
        let edgeId = this_.visNetwork.getEdgeAt(params.pointer.DOM);
        if(typeof nodeId !== "undefined"){
          const node = this_.visData.nodes.get(nodeId);
          this_.$store.state.graphVisData.selectedNode = node;
          console.log(node);
        }else if(typeof edgeId !== "undefined"){
          const edge = this_.visData.nodes.get(edgeId);
          this_.$store.state.graphVisData.selectedEdge = edge;
        }else {
          this.contextMenuData = null;
          this_.$store.state.graphVisData.selectedNode = null;
          console.log("点击图谱空白");
        }
      });
    },
    switchNodeExpandState(nodeId, isExpanded){
      const flag = typeof isExpanded !== "undefined" || isExpanded === null;
      console.log(flag, isExpanded)
      if(typeof nodeId !== "undefined"){
        let node = this.visData.nodes.get(nodeId);
        if(!node.expanded){
          if(flag){
            if(isExpanded){
              this.addData(node.children);
              node.expanded = true;
            }
          }else {
            this.addData(node.children);
            node.expanded = true;
          }
        }else {
          if(flag){
            if(!isExpanded){
              this.removeData(node.children);
              node.expanded = false;
            }
          }else {
            this.removeData(node.children);
            node.expanded = false;
          }
        }
      }
    },
    addData (newData){
      const this_ = this;
      newData.nodes.forEach((N) =>{
        N.label = N.name;
        N.group = "concept";
        if(this_.visData.nodeIds.indexOf(N.id) === -1){
          this_.visData.nodes.add(N);
          this_.visData.nodeIds.push(N.id);
        }
      });
      newData.relationships.forEach((N) =>{
        N.relationArray.forEach((SN)=>{
          SN.label = SN.ifSuperClass ? "有子概念" :  SN.name;
          SN.group = null;
          if(typeof SN.from ==="undefined" || SN.from === null){
            SN.from = SN.source;
            SN.to = SN.target;
          }
          console.log(SN)

          if(this_.visData.edgeIds.indexOf(SN.id) === -1){
            this_.visData.edges.add(SN);
            this_.visData.edgeIds.push(SN.id);
          }
        })

      });
    },
    removeData(data){
      const this_ = this;
      if(data.nodes){
        data.nodes.forEach((N) =>{
          if(this_.visData.nodeIds.indexOf(N.id) !== -1){
            this_.visData.nodes.remove(N);
            this_.visData.nodeIds.splice(this_.visData.nodeIds.indexOf(N.id), 1);
          }
        });
      }
      if(data.edges){
        data.edges.forEach((N) =>{
          if(this_.visData.edgeIds.indexOf(N.id) !== -1){
            this_.visData.edges.remove(N);
            this_.visData.edgeIds.splice(this_.visData.edgeIds.indexOf(N.id), 1);
          }
        });
      }
    },
    handleContextMenuClickEvent: function (action, data){
      console.log("右键菜单选择", action, data);
      if(action === "delete"){
        const removeData = this.contextMenuData.type === 'node' ? {nodes:[this.contextMenuData.value]} :{ edges:[this.contextMenuData.value]};
        this.removeData(removeData);
        console.log(removeData)
      }else if(action === "edit"){
        console.log("点击编辑菜单")
        if(this.contextMenuData.type === 'node'){
          this.editNodePanelVisible = true;
          this.nodeEditType='编辑节点'
        }
      }else if(action === "addNode"){
        this.contextMenuData = {
          value: {
            id: 'tmpId01',
            label: '',
            name:this.label,
          },
          type: 'node'
        }
        this.editNodePanelVisible = true;
        this.nodeEditType='添加节点'
      }else if(action === "addEdge"){
        console.log("添加关系");
        this.visNetwork.addEdgeMode();
      }

    }
  },
  watch: {
    newVisData:{
      immediate: true,
      handler: function() {
        if(this.newVisData && this.newVisData.nodes){
          this.addData(this.deepCopy(this.newVisData));
        }
      }
    },
    selectedNode(){
      this.selectNodeByPath(this.selectedNode);
    }
  },
  mounted() {
    this.$nextTick(()=>this.initVisNetwork());
  },
}
</script>

<style scoped>
#contextMenuContainer{
  position: absolute;
  left: 500px;
  top: 500px;
  display: none;
}
#nodeEditPanelContainer{
  position: absolute;
  left: 500px;
  top: 500px;
}

#network{
  outline: none;
}
</style>
<style>
.vis-network{
  outline: none;
}
</style>