<script>
export default {
  name: 'SelectableTag',
  props: {
    text: String,
    closable: Boolean,
    type: String,
    hit: Boolean,
    disableTransitions: Boolean,
    color: String,
    size: String,
    effect: {
      type: String,
      default: 'plain',
      validator(val) {
        return ['dark', 'light', 'plain'].indexOf(val) !== -1;
      }
    },
    data:{
      type: String
    },
    checked:{
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleClose(event) {
      event.stopPropagation();
      this.$emit('close', event);
    },
    handleClick() {
      this.mchecked = !this.mchecked;
      this.$emit('click', this.data, this.mchecked);
    }
  },
  data(){
    return {
      mchecked: false,
      meffect: 'plain',
    }
  },
  watch:{
    checked(){
      this.mchecked = this.checked;
    },
    effect(){
      this.meffect = this.effect;
    },
    mchecked (){
      if(this.mchecked){
        this.meffect = 'dark';
      }else{
        this.meffect = 'plain';
      }
    }
  },
  computed: {
    tagSize() {
      return this.size || (this.$ELEMENT || {}).size;
    }
  },
  render() {
    const { type, tagSize, hit, meffect } = this;
    const classes = [
      'el-tag',
      type ? `el-tag--${type}` : '',
      tagSize ? `el-tag--${tagSize}` : '',
      meffect ? `el-tag--${meffect}` : '',
      hit && 'is-hit'
    ];
    const tagEl = (
        <span
            class={ classes }
            style={{ backgroundColor: this.color }}
            on-click={ this.handleClick }>
          { this.$slots.default }
          {
            this.closable && <i class="el-tag__close el-icon-close" on-click={ this.handleClose }></i>
          }
        </span>
    );

    return this.disableTransitions ? tagEl : <transition name="el-zoom-in-center">{ tagEl }</transition>;
  }
};
</script>
