<template>
  <div class="layout">
    <!--iView自带元素，需使用Vue的style定义方式才能传递到编译后的元素上-->
    <el-container :style="{height:'100%'}">
      <el-header :height="headerHeight" :style="{ background:'#909399',padding:'0',position: 'fixed',width:'100%'}">
        <PageHeader></PageHeader>
      </el-header>
      <el-container :style="{height:'100%',paddingTop:headerHeight}">
        <el-aside :style="{padding: '1px', background:'#ffffff', borderRight: '1px solid #d3d3d3'}">
          <el-tabs class="left-tabs" :tab-position="'left'" style="height: 100%;">
            <el-tab-pane :style="{overflowY:'auto'}">
              <span class="side-tab-title" slot="label">模型选择</span>
              <ModelAndVersionSelector  :model-and-versions="modelAndVersions" @on-select-model-and-version="onSelectModelAndVersion"/>
            </el-tab-pane>

            <el-tab-pane >
              <span class="side-tab-title" slot="label">树结构编辑</span>
              <TreeEdit @node-select-event="handleTreeEditNodeSelect" ></TreeEdit>
            </el-tab-pane>

            <el-tab-pane >
              <span class="side-tab-title" slot="label">角色管理</span>
              角色管理
            </el-tab-pane>
          </el-tabs>

        </el-aside>
        <el-main :style="{padding: '0'}">
          <el-container :style="{height:'100%'}">
            <el-header :style="{height:'auto'}">

            </el-header>
            <!--画布-->
            <el-main
                :style="{padding: '0px', minHeight: '280px', background: '#fff', height:'100%', borderTop: '1px solid #d3d3d3'}">
              <EvidenceCalculationConfigPanel :style="{float:'left',top:'90px',position: 'fixed',zIndex:1}"/>
              <!--可信证据收集界面-->
              <EvidenceImportPanel :style="{float:'left',top:'90px', left:'700px', position: 'fixed',zIndex:1}"/>
              <EvidenceExtractPanel :style="{float:'left',top:'400px', left:'700px', position: 'fixed',zIndex:1}"/>
              <EvidenceInferPanel :style="{float:'left',top:'650px', left:'700px', position: 'fixed',zIndex:1}"/>
              <EvidenceEvaluationPanel :style="{float:'left',top:'650px', position: 'fixed',zIndex:1}"/>
              <SearchForm :style="{float:'left'}" :tags="this.$store.state.tags" class="search-form"
                          @search-event="search"/>
              <NetworkVis></NetworkVis>
            </el-main>
          </el-container>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import SearchForm from "@/components/SearchForm";
import ModelAndVersionSelector from "@/components/ModelAndVersionSelector";
import NetworkVis from "@/components/NetworkVis";
import TreeEdit from "@/components/TreeEdit";
import PageHeader from "@/components/PageHeader";
import EvidenceCalculationConfigPanel from "@/accept-components/EvidenceCalculationConfigPanel";
import EvidenceImportPanel from "@/accept-components/EvidenceImportPanel";
import EvidenceExtractPanel from "@/accept-components/EvidenceExtractPanel";
import EvidenceInferPanel from "@/accept-components/EvidenceInferPanel";
import EvidenceEvaluationPanel from "@/accept-components/EvidenceEvaluationPanel";

export default {
  name: "Main",
  components: {
    EvidenceCalculationConfigPanel,
    EvidenceImportPanel, EvidenceExtractPanel, EvidenceInferPanel, EvidenceEvaluationPanel,
    SearchForm, ModelAndVersionSelector, NetworkVis, TreeEdit, PageHeader
  },
  data() {
    return {
      headerHeight: '40px',
      allVersionApi: "/v1/model-version/allVersion",
      conceptModelApi: "/v1/concept-model",
      tagsApi: "/v1/onto/chooseTags",
      ontoModelApi: "/v1/onto/showOntModel",
      coreConceptApi: "/v1/onto/chooseCore",
      models:[],
      modelAndVersions: [],
      selectedModelVersion: {},

    }
  },
  watch: {
    modelAndVersions: function (){
      if(this.$store.state.isTestMode){
        for(let i in this.modelAndVersions){
          let mv = this.modelAndVersions[i];
          if(mv.label === "贵阳市政务领域概念模型"){
            this.selectedModelVersion = mv.children[0].children[0].modelVersion;
          }
        }
      }
    },
    tags: function (){
    },
    /*监听selectedModelVersion自动获取新tags*/
    selectedModelVersion: function(){
      this.$axios.post(this.serverUrl +  this.tagsApi, this.$qs.stringify({
        expertId: this.expertId,
        modelId: this.selectedModelVersion.id,
        version: this.selectedModelVersion.version
      }), {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',//将表单数据传递转化为form-data类型
          'dataType': "json"
        }
      }).then((res)=>{
        this.$store.state.tags = res.data;
        console.log(res.data)
      })
    },
  },
  methods:{
    handleTreeEditNodeSelect(nodePath){
      console.log("handleTreeEditNodeSelect", nodePath);
    },
    getModelVersions: function (){
      this.models.forEach((M)=>{
        this.$axios.get(this.serverUrl + this.allVersionApi,{
          params: {
            expertId: this.expertId,
            modelId: M.id
          }
        }).then((res)=>{
          let model = {
            label: M.name,
            expand: false,
            contextmenu: false,
            children: []
          }

          let pubVersions = {
            label: "公有版本",
            expand: false,
            contextmenu: false,
            children: []
          }

          let priVersions = {
            label: "私有版本",
            expand: false,
            contextmenu: false,
            children: []
          }
          res.data.privateVersion.forEach((V)=>{
            priVersions.children.push({
              label: V.version,
              expand: false,
              contextmenu: false,
              children: [],
              modelVersion:{
                id: M.id,
                name: M.name,
                version: V.version
              },
              clickable: true
            })
          })
          res.data.publicVersion.forEach((V)=>{
            pubVersions.children.push({
              label: V.version,
              expand: false,
              contextmenu: false,
              children: [],
              modelVersion:{
                id: M.id,
                name: M.name,
                version: V.version
              },
              clickable: true
            })
          });
          model.children.push(pubVersions);
          model.children.push(priVersions);
          this.modelAndVersions.push(model);
        })
      });
    },
    onSelectModelAndVersion: function (selectedData){
      this.selectedModelVersion = selectedData.modelVersion;
    },
    search(data){
      this.$axios.post(this.serverUrl + this.ontoModelApi, this.$qs.stringify({
        expertId: this.expertId,
        keyWord: data.keyword === "" ? "*" : data.keyword,
        modelId: this.selectedModelVersion.id,
        tags: data.selectedTags,
        version: this.selectedModelVersion.version
      }, {indices: false}),{
          headers:{
            // 'Access-Control-Allow-Origin':'*',  //解决cors头问题
            // 'Access-Control-Allow-Credentials':'true', //解决session问题
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',//将表单数据传递转化为form-data类型
            'dataType': "json"
          }
      }).then((res)=>{
        //console.log(res);
        this.$store.state.graphVisData.newVisData = {
          nodes: res.data.nodes,
          relationships: res.data.relationships,
        }
      })
    },
    readCoreConcept: function (){
      this.$axios.post(this.serverUrl + this.coreConceptApi).then((response)=>{
        this.$store.state.coreConcepts = response.data;
        console.log("main: coreConcepts",this.$store.state.coreConcepts)
      })
    }
  },
  mounted: function (){
    this.$nextTick(function (){
      this.$axios.get(this.serverUrl + this.conceptModelApi).then((response)=>{
        this.models = response.data;
        this.getModelVersions();
        this.readCoreConcept();
      });
    })
  },
  computed:{
  },
  props: {
    serverUrl: {
      type: String,
      default: "http://localhost:7003"
    },
    modelId: {
      type: Number,
      default: 1
    },
    expertId: {
      type: Number,
      default: 1
    }
  },

}
</script>
<style>
.search-form{
  position: fixed;
  z-index: 1;
}
.left-tabs  .el-tabs__header{
  margin-right: 0!important;
}
.left-tabs  .el-tabs__item{
  text-align: left;
  line-height: 180px;
  height: 100px;
  width: 26px;
  padding: 0;
  margin-left: 0!important;
}
</style>

