<template>
  <el-form ref="formInline" :model="searchForm" inline size="mini">
    <el-form-item :style="{margin:this.searchFormMargin+' '+this.searchFormMargin}">
      <el-input type="text" v-model="searchForm.keyword" placeholder="输入关键词">

        <el-dropdown style="margin-left: 0px" slot="prepend" trigger="click" placement="bottom-start">
          <el-button type="primary">
            领域
          </el-button>
          <el-dropdown-menu slot="dropdown" :style="{height: 'auto', width:'auto', maxHeight: '500px', overflowY: 'auto',padding:'0',margin:'0'}">
            <div :style="{width:'auto', position:'relative',padding:'5px'}">
              <el-row :key="JSON.stringify(tags)" :name="tags" v-for="tags in this.getSplitTag" :style="{width:'auto'}">
                <el-col :span="4" :key="tag" :name="tag" v-for="tag in tags" :style="{padding: '1px'}">
                  <SelectableTag :style="{width:'100%',overflow:'hidden',textOverflow:'ellipsis'}" size="mini" :hit="false"  effect="plain" :name="tag" :data="tag" @click="onTagChange">{{ tag }}</SelectableTag>
                </el-col>
              </el-row>
            </div>
          </el-dropdown-menu>
        </el-dropdown>

        <el-button type="primary" slot="append" @click="handleSubmit()">搜索</el-button>
      </el-input>
    </el-form-item>
  </el-form>
</template>

<script>
import SelectableTag from "@/components/SelectableTag";

export default {
  components: {SelectableTag},
  name: "SearchForm",
  props:{
    tags: {
      type: Array,
      default: function(){
        return ["test1", "test2"]
      }
    }
  },
  data () {
    return {
      searchForm: {
        selectedTags:[],
        keyword: ''
      },
      searchFormMargin: "5px",
      tagsPerRow: 6,
      splitTag: this.getSplitTag,
      selectedTagNum: 0
    }
  },
  mounted() {

  },
  watch:{
    tags: function (){
    }
  },
  computed: {
    getSplitTag: function (){
      let splitTag_ = [];
      let i = 0;
      let tmpTags = [];
      let tags_ = this.tags;
      tags_.forEach((T)=>{
        if(i !== 0 && i % this.tagsPerRow === 0){
          splitTag_.push(JSON.parse(JSON.stringify(tmpTags)));
          tmpTags = [];
        }
        tmpTags.push(T);
        i++;
      });
      //console.log("splitTag_",splitTag_);
      return splitTag_;
    }
  },
  methods: {
    handleSubmit() {
      if(this.searchForm.selectedTags.length === 0 && this.$store.state.isTestMode){
        while(this.selectedTagNum <= 26){
          this.selectedTagNum++;
          this.searchForm.selectedTags.push(this.tags[this.selectedTagNum]);
          //console.log("选择的tag", this.searchForm.selectedTags);
        }
      }
      this.$emit("search-event", this.searchForm);
    },
    onTagChange(name, checked){
      console.log(name, checked);
      if(checked){
        if(!this.searchForm.selectedTags.includes(name)){
          this.searchForm.selectedTags.push(name);
        }
      }else {
        this.searchForm.selectedTags = this.searchForm.selectedTags.slice(this.searchForm.selectedTags.indexOf(name), 1)
      }
    },
  },
}
</script>

