<template>
  <el-row :style="{margin:'5px 30px 5px 5px'}" >
    <el-button v-on:click="handleExpandAllNode" icon="el-icon-s-grid" class="network-setting-button" circle/>
    <el-popover
        placement="bottom"
        width="200"
        trigger="click">
      <div ref="data" :style="{margin:'10px'}">
        <div class="block">
          <span class="demonstration">节点质量</span>
          <el-slider
              v-model="nodeGravityMapValue"
              :step="5"
              show-stops>
          </el-slider>
        </div>
      </div>
      <el-button slot="reference" icon="el-icon-setting" class="network-setting-button" circle/>
    </el-popover>
  </el-row>
</template>

<script>
export default {
  name: "NetworkSettingToolbar",
  data(){
    return {
      localVisOption: null,
      nodeGravityMapValue: 0
    }
  },
  props:{
    visOption: Object
  },
  watch:{
    nodeGravityMapValue(){
      this.localVisOption.physics.barnesHut.gravitationalConstant = (this.nodeGravityMapValue + 1) * -200;
      this.$emit('vis-option-update-event', JSON.parse(JSON.stringify(this.localVisOption)));
    },
    visOption(){
      this.localVisOption = JSON.parse(JSON.stringify(this.visOption));
      this.nodeGravityMapValue = this.localVisOption.physics.barnesHut.gravitationalConstant/-200 - 1;
    }
  },
  methods:{
    handleExpandAllNode(){
      this.$emit("expand-all-node-event")
    }
  },
  mounted() {
    console.log('this.visOption',this.visOption)
    this.localVisOption = JSON.parse(JSON.stringify(this.visOption));
    this.nodeGravityMapValue = this.localVisOption.physics.barnesHut.gravitationalConstant/-400 - 1;
  }
}
</script>

<style scoped>
.network-setting-button{
  margin-right: 10px;
}
</style>