<template>
  <el-dialog :title="nodeEditType" :visible.sync="localEditNodePanelVisible" :width="'30%'">
    <div :style="{padding:'5px 0',margin:'0'}" :body-style="{padding:'0px'}">
      <el-form ref="nodeData" size="mini" :style="{width:'100%',maxHeight:'600px',overflowY:'auto',marginRight:'0'}" :model="nodeData">
        <el-tabs v-model="activeName" type="border-card" value="1" >
          <el-tab-pane label="名称" name="1">
            <el-form-item label="名称">
              <el-input v-model="nodeData.name"></el-input>
            </el-form-item>
            <el-form-item label="描述">
              <el-input type="textarea" v-model="nodeData.comment"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="父类" name="2">角色管理</el-tab-pane>
          <el-tab-pane label="核心概念" name="3">角色管理</el-tab-pane>
          <el-tab-pane label="领域" name="4">角色管理</el-tab-pane>
          <el-tab-pane label="城市" name="5">
            <el-form-item label="输入概念所属的城市">
              <el-input v-model="nodeData.group"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="属性" name="6">
            <el-form-item
                v-for="dataProp in nodeData.properties"
                :key="dataProp.uri">
              <el-col :span="11">
                <el-input v-model="dataProp.name" :style="{width:'100%'}"></el-input>
              </el-col>
              <el-col :span="2"></el-col>
              <el-col :span="11">
                <el-input v-model="dataProp.type" :style="{width:'100%'}"></el-input>
              </el-col>
            </el-form-item>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="localEditNodePanelVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleNodeEditSubmit">确 定</el-button>
    </div>
  </el-dialog>

</template>

<script>
export default {
  name: "NodeEditPanel",
  props:{
    nodeData:{
      type:Object,
      default(){
        return {
          name: "",
          group: ""
        }
      }
    },
    activeName: {
      type: String,
      default: "1"
    },
    editNodePanelVisible:{
      type:Boolean,
      default: false
    }
  },
  data(){
    return{
      localEditNodePanelVisible:false,
    }
  },
  methods:{
    handleNodeEditSubmit(){
      this.localEditNodePanelVisible = false;
      this.$emit('edit-node-submit-event', this.nodeData);
    }
  },
  watch: {
    editNodePanelVisible(){
      this.localEditNodePanelVisible = this.editNodePanelVisible;
    }
  }
}
</script>

