    <MyMenuItem :action="'edit'" v-if="menuItem.indexOf('edit') !== -1" @my-menu-item-click="handleMyMenuItemClick" :style="{padding:'0 5px 0 5px',margin:'0', width:'70px'}">编辑</MyMenuItem>
