<template>
  <el-tree :data="modelAndVersions"
           @node-click="handleNodeClick"
           :style="{textAlign:'left',height:'100%',overflowY:'auto'}"></el-tree>
</template>
<script>
export default {
  name: "ModelAndVersionSelector",
  props:{
    modelAndVersions: {
      type: Array
    }
  },
  data () {
    return {
      contextData: null
    }
  },
  mounted() {
  },
  methods: {
    handleNodeClick(selectedData){
      if(selectedData.clickable){
        this.$emit("on-select-model-and-version", selectedData);
      }
    },
    handleContextMenu (data) {
      this.contextData = data;
    },
    handleContextMenuEdit () {
      this.$Message.info('Click edit of' + this.contextData.title);
    },
    handleContextMenuDelete () {
      this.$Message.info('Click delete of' + this.contextData.title);
    }
  }
}
</script>



