<template>
  <el-card :body-style="{textAlign:'left', overflowY:'auto',height:'auto'}">
    <div slot="header">
      <span>知识验证</span>
      <el-button style="float: right;padding: 3px 0" type="text">x</el-button>
    </div>
    <el-form size="mini">
      <div v-for="evidence in data.EvidenceExtract.evidences" :key="evidence">
        <el-form-item v-if="evidence.type==='file'">
          <el-col :span="12">
            {{ evidence.name }}
          </el-col>
          <el-col :span="12">
            <el-upload
                :before-remove="beforeRemove"
                :file-list="evidence.value"
                :limit="1"
                :on-exceed="handleExceed"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                action="https://jsonplaceholder.typicode.com/posts/"
                class="upload-demo"
                multiple>
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-col>
        </el-form-item>
        <el-form-item v-if="evidence.type==='comment'">
          <el-col :span="12">
            {{ evidence.name }}
          </el-col>
          <el-col :span="12">
            <el-input v-model="evidence.value" :rows="2"
                      placeholder="请输入内容" type="textarea"></el-input>
          </el-col>
        </el-form-item>
      </div>
    </el-form>
    <div id="confirm_buttons" style="text-align: center">
      <el-button round size="mini" type="success" @click="open">保存</el-button>
      <el-button round size="mini" type="danger">关闭</el-button>
    </div>
  </el-card>
</template>

<script>
export default {
  name: "EvidenceExtractPanel",
  data() {
    return {
      visible: false,
      data: {
        EvidenceExtract: {
          name: "知识验证",
          evidences: {
            Source: {
              name: "三方验证报告",
              type: "file",
              value: ""
            },
            Type: {
              name: "用户反馈信息",
              type: "comment",
              value: ""
            },
          }
        },
      }
    }
  },
  methods: {
    open() {
      this.$alert('是否保存', '保存确认', {
        confirmButtonText: '确定',
        callback: () => {
          this.$message({
            type: 'success',
            message: `保存成功!`
          });
        }
      });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    beforeRemove(file) {
      return this.$confirm(`确定移除 ${file.name}？`);
    }
  }
}
</script>

