  watch: {
    modelAndVersions: function (){
      if(this.$store.state.isTestMode){
        for(let i in this.modelAndVersions){
          let mv = this.modelAndVersions[i];
          if(mv.label === "贵阳市政务领域概念模型"){
            this.selectedModelVersion = mv.children[0].children[0].modelVersion;
          }
        }
      }
    },
    tags: function (){
    },
    /*监听selectedModelVersion自动获取新tags*/
    selectedModelVersion(){
      this.getTags();
    }
  },
  methods:{
    test(){
      this.$store.state.compareGraphVisData.newVisDataLeft = JSON.parse(JSON.stringify({
        nodes: this.testData.nodes,
        relationships: this.testData.relationships,
      }))
      this.$store.state.compareGraphVisData.newVisDataRight = JSON.parse(JSON.stringify({
        nodes: this.testData.nodes,
        relationships: this.testData.relationships,
      }))
    },
    handleCompareVersionSelect(selectedCompareVersions){
      this.selectedModelVersion = {
        versionLeft: selectedCompareVersions.versionLeft[1],
        versionRight: selectedCompareVersions.versionRight[1],
        modelId: this.selectedModelVersion.modelId,
      }
      console.log("handleCompareVersionSelect",selectedCompareVersions)
      console.log("handleCompareVersionSelect",this.selectedModelVersion)
    },
    getModelVersions: function (){
      this.models.forEach((M)=>{
        this.$axios.get(this.serverUrl + this.allVersionApi,{
          params: {
            expertId: this.expertId,
            modelId: M.id
          }
        }).then((res)=>{
          let model = {
            label: M.name,
            modelId: M.id,
            expand: false,
            contextmenu: false,
            childrenData: [],
            clickable: true
          }

          let pubVersions = {
            label: "公有版本",
            expand: false,
            contextmenu: false,
            children: []
          }

          let priVersions = {
            label: "私有版本",
            expand: false,
            contextmenu: false,
            children: []
          }
          res.data.privateVersion.forEach((V)=>{
            priVersions.children.push({
              label: V.version,
              expand: false,
              contextmenu: false,
              children: [],
              modelVersion:{
                id: M.id,
                name: M.name,
                version: V.version
              },
              clickable: true
            })
          })
          res.data.publicVersion.forEach((V)=>{
            pubVersions.children.push({
              label: V.version,
              expand: false,
              contextmenu: false,
              children: [],
              modelVersion:{
                id: M.id,
                name: M.name,
                version: V.version
              },
              clickable: true
            })
          });
          model.childrenData.push(pubVersions);
          model.childrenData.push(priVersions);
          this.modelAndVersions.push(model);
        })
      });
    },
    onSelectModelAndVersion: function (model){
      console.log("onSelectModelAndVersion",model)
      this.selectedModelVersion = {
        modelId: model.modelId,
      };
      //this.selectedModelVersion = model;
      const children1 = [];
      model.childrenData[0].children.forEach((V)=>{
        children1.push({
          label: V.label,
          value: V.label,
          modelVersion: V.modelVersion
        })
      });
      const children2 = [];
      model.childrenData[1].children.forEach((V)=>{
        children2.push({
          label: V.label,
          value: V.label,
          modelVersion: V.modelVersion
        })
      });

      this.compareVersionSelectDialogData = {
        title: "选择要对比的版本",
        visible: true,
        options: [
          {
            value: model.childrenData[0].label,
            label: model.childrenData[0].label,
            children: children1
          },
          {
            value: model.childrenData[1].label,
            label: model.childrenData[1].label,
            children: children2
          },
        ]
      }

    },
    search(data){
      // console.log(data);
      // 左视图
      this.$axios.post(this.serverUrl + this.ontoModelApi, this.$qs.stringify({
        expertId: this.expertId,
        keyWord: data.keyword === "" ? "*" : data.keyword,
        modelId: this.selectedModelVersion.modelId,
        tags: data.selectedTags,
        version: this.selectedModelVersion.versionLeft
      }, {indices: false}),{
        headers:{
          // 'Access-Control-Allow-Origin':'*',  //解决cors头问题
          // 'Access-Control-Allow-Credentials':'true', //解决session问题
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',//将表单数据传递转化为form-data类型
          'dataType': "json"
        }
      }).then((res)=>{
        //console.log(res);
        this.$store.state.compareGraphVisData.newVisDataLeft = JSON.parse(JSON.stringify({
          nodes: res.data.nodes,
          relationships: res.data.relationships,
        }))
      })
      // 右视图
      this.$axios.post(this.serverUrl + this.ontoModelApi, this.$qs.stringify({
        expertId: this.expertId,
        keyWord: data.keyword === "" ? "*" : data.keyword,
        modelId: this.selectedModelVersion.modelId,
        tags: data.selectedTags,
        version: this.selectedModelVersion.versionRight
      }, {indices: false}),{
        headers:{
          // 'Access-Control-Allow-Origin':'*',  //解决cors头问题
          // 'Access-Control-Allow-Credentials':'true', //解决session问题
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',//将表单数据传递转化为form-data类型
          'dataType': "json"
        }
      }).then((res)=>{
        //console.log(res);
        this.$store.state.compareGraphVisData.newVisDataRight = JSON.parse(JSON.stringify({
          nodes: res.data.nodes,
          relationships: res.data.relationships,
        }))
      })
    },
    readCoreConcept: function (){
      this.$axios.post(this.serverUrl + this.coreConceptApi).then((response)=>{
        this.$store.state.coreConcepts = response.data;
        console.log("main: coreConcepts",this.$store.state.coreConcepts)
      })
    },
    getTags(){
      // 左视图
      this.$axios.post(this.serverUrl +  this.tagsApi, this.$qs.stringify({
        expertId: this.expertId,
        modelId: this.selectedModelVersion.modelId,
        version: this.selectedModelVersion.versionLeft
      }), {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',//将表单数据传递转化为form-data类型
          'dataType': "json"
        }
      }).then((res)=>{
        this.$store.state.tags = res.data;
      })
    },
  },
  mounted: function (){
    this.$nextTick(function (){
      this.$axios.get(this.serverUrl + this.conceptModelApi).then((response)=>{
        this.models = response.data;
        this.getModelVersions();
        this.readCoreConcept();
      });
    })
    this.test()
  },
  computed:{
  },
  props: {
    serverUrl: {
      type: String,
      default: "http://localhost:7003"
    },
    modelId: {
      type: Number,
      default: 1
    },
    expertId: {
      type: Number,
      default: 1
    }
  },

}
